import React, { Component } from "react";
import { BiSort } from "react-icons/bi";
import { ImFilesEmpty } from "react-icons/im";
import { connect } from "react-redux";
import {
  API_GetPlacementBySchool,
  Auth,
  Basic,
  FC_GetAcademics,
  FC_GetPlacementByDistrict,
  FC_GetReportByDistrict,
  FC_GetReportByInstitute,
  FC_GetReportBySchool,
  FT_GetPlacementByDistrict,
  Placement,
  TeacherData,
} from "../../../actions";
import DashboardContainer from "../../../components/DashboardContainer/DashboardContainer";
import Heading from "../../../components/Heading/Heading";
import { StoreState } from "../../../reducers";
import {
  DATE,
  isEmptyOrSpaces,
  MONTHS_LIST,
  search,
  sortThisArray,
} from "../../../utils/functions";
import {
  defaultPlacementReportActiveContent,
  FC_GetHierarchStrings,
  FC_GetStringValue,
  FC_isDateValid,
  forReportRoles,
  PlacementReportActiveContentInterface,
  PlacementReportActiveContentKeyInterface,
  ReportAcademicYearType,
  reportToPrintListType,
  tablePageLimit,
} from "../../../types/PlacementReportTypes";
import TextInput from "../../../components/Inputs/TextInput";
import Modal, { ModalSize, Themes } from "../../../components/Modal/Modal";
import { MdCheckBox, MdCheckBoxOutlineBlank } from "react-icons/md";
import ExportToExcel from "../../../components/GenerateReport/ExportToExcel";
import InputSelect from "../../../components/Inputs/InputSelect";
import TableLoading from "../../../components/TableLoading/TableLoading";
import {
  HiChevronDoubleLeft,
  HiChevronDoubleRight,
  HiChevronLeft,
  HiChevronRight,
} from "react-icons/hi";

const TodayDate = new Date();

interface PlacementReportProps {
  // REDUX PROPS ======
  auth: Auth;
  basic: Basic;
  placement: Placement;
  FC_GetPlacementByDistrict: FT_GetPlacementByDistrict;
  FC_GetAcademics: Function;
}

interface PlacementReportState {
  loading: boolean;
  loadingAcademics: boolean;
  downloading: boolean;
  sortOrder: "ASC" | "DESC";
  sortProperty: string;
  searchInput: string;
  reportTitle: string;
  selectAll: boolean;
  generateReport: boolean;
  formError: {
    element: string;
    msg: string;
  } | null;
  reportContent: PlacementReportActiveContentInterface[];
  reportToPrint: reportToPrintListType[] | null;
  selectedMonth: string;
  reportAPIData: API_GetPlacementBySchool[] | null;
  currentPage: number;
  tablePageLength: number;
  selectedAcademic: string;
}

export class _PlacementReport extends Component<
  PlacementReportProps,
  PlacementReportState
> {
  constructor(props: PlacementReportProps) {
    super(props);

    this.state = {
      loading: true,
      loadingAcademics: false,
      downloading: false,
      sortOrder: "ASC",
      sortProperty: "",
      searchInput: "",
      reportTitle: "",
      selectAll: true,
      generateReport: false,
      formError: null,
      reportContent: defaultPlacementReportActiveContent,
      reportToPrint: null,
      selectedMonth: MONTHS_LIST[TodayDate.getMonth()],
      reportAPIData: null,
      currentPage: 1,
      tablePageLength: tablePageLimit,
      selectedAcademic: this.props.basic.academic_calender
        ? this.props.basic.academic_calender.academic_year_id
        : "",
    };
  }

  setDownloading = (value: boolean) => {
    this.setState({ downloading: value });
  };

  setSortProperty = (value: string) => {
    this.setState({ sortProperty: value });
  };

  setSortOrder = (value: "ASC" | "DESC") => {
    this.setState({ sortOrder: value });
  };

  setSearchInput = (value: string) => {
    this.setState({ searchInput: value });
  };

  setReportTitle = (value: string) => {
    this.setState({ reportTitle: value });
  };

  setSelectAll = (value: boolean) => {
    this.setState({ selectAll: value });
  };

  setFormError = (
    value: {
      element: string;
      msg: string;
    } | null
  ) => {
    this.setState({ formError: value });
  };

  setSort = (thisProperty: string) => {
    if (
      this.state.sortProperty === thisProperty &&
      this.state.sortOrder === "ASC"
    ) {
      this.setSortOrder("DESC");
    } else {
      this.setSortOrder("ASC");
    }
    this.setSortProperty(thisProperty);
  };

  setGenerateReport = (value: boolean) => {
    this.setState({ generateReport: value });
  };

  setReportContent = (value: PlacementReportActiveContentInterface[]) => {
    this.setState({ reportContent: value });
  };

  setCurrentPage = (value: number) => {
    this.setState({ currentPage: value });
  };

  loadDataFormAPI = (month: string, academic_id: string) => {
    if (
      this.props.auth.district &&
      this.props.auth.role &&
      forReportRoles.district.includes(this.props.auth.role.role)
    )
      FC_GetReportByDistrict(
        this.props.auth.district.district_code,
        academic_id,
        month,
        (status, msg, data) => {
          this.setState({
            loading: status,
            reportAPIData: null,
          });
          console.log(
            "=================reportsss DISTRICT LEVEL ==================="
          );
          console.log({ report: data });
          console.log(
            "=================reportsss DISTRICT LEVEL ==================="
          );
          if (status === false && data !== null) {
            this.setState({
              reportAPIData: data,
            });
          } else if (!status && !isEmptyOrSpaces(msg)) {
            console.error({ status, msg });
            // this.setState({
            //   loading: false,
            //   error: {
            //     msg: msg,
            //     target: "main-error",
            //   },
            // });
          }
        }
      );
    else if (
      this.props.auth.school &&
      this.props.auth.role &&
      forReportRoles.school.includes(this.props.auth.role.role)
    )
      FC_GetReportBySchool(
        this.props.auth.school.school_code,
        academic_id,
        (status, msg, data) => {
          this.setState({
            loading: status,
            reportAPIData: null,
          });
          if (status === false && data !== null) {
            console.log(
              "=================reportsss SCHOOL LEVEL ==================="
            );
            console.log({ report: data });
            console.log(
              "=================reportsss SCHOOL LEVEL ==================="
            );
            this.setState({
              reportAPIData: data.filter((item) => item.month === month),
            });
          } else if (!status && !isEmptyOrSpaces(msg)) {
            console.error({ status, msg });
            // this.setState({
            //   loading: false,
            //   error: {
            //     msg: msg,
            //     target: "main-error",
            //   },
            // });
          }
        }
      );
    else if (
      this.props.auth.role &&
      forReportRoles.country.includes(this.props.auth.role.role)
    )
      FC_GetReportByInstitute(academic_id, month, (status, msg, data) => {
        this.setState({
          loading: status,
          reportAPIData: null,
        });
        if (status === false && data !== null) {
          console.log(
            "=================reportsss COUNTRY LEVEL ==================="
          );
          console.log({ report: data });
          console.log(
            "=================reportsss COUNTRY LEVEL ==================="
          );
          this.setState({
            reportAPIData: data.filter((item) => item.month === month),
          });
        } else if (!status && !isEmptyOrSpaces(msg)) {
          console.error({ status, msg });
          // this.setState({
          //   loading: false,
          //   error: {
          //     msg: msg,
          //     target: "main-error",
          //   },
          // });
        }
      });
  };

  componentDidMount = () => {
    if (this.props.basic.academic_calender)
      this.loadDataFormAPI(
        this.state.selectedMonth,
        this.props.basic.academic_calender.academic_year_id
      );
    if (
      this.props.placement.academics === null ||
      this.props.placement.academics.length === 0
    )
      this.props.FC_GetAcademics(
        (status: boolean, data: ReportAcademicYearType[], msg: string) => {
          this.setState({ loadingAcademics: status });
          // if (!status && !isEmptyOrSpaces(msg)) this.setState({});
        }
      );
    else this.setState({ loadingAcademics: false });
  };

  updateDataToPrint = () => {
    // UPDATE REPORT TITLE ========================
    if (
      this.props.auth.district &&
      this.props.auth.role &&
      forReportRoles.district.includes(this.props.auth.role.role)
    ) {
      let title =
        "Placement Report - " +
        this.props.auth.district.district_name +
        " district";
      if (this.props.basic.academic_calender) {
        title =
          title +
          " - " +
          this.props.basic.academic_calender.academic_year_name +
          " - Month of " +
          this.state.selectedMonth;
      }
      this.setReportTitle(title);
    } else if (
      this.props.auth.school &&
      this.props.auth.role &&
      forReportRoles.school.includes(this.props.auth.role.role)
    ) {
      let title =
        "Placement Report - " + this.props.auth.school.school_name + " school";
      if (this.props.basic.academic_calender) {
        title =
          title +
          " - " +
          this.props.basic.academic_calender.academic_year_name +
          " - Month of " +
          this.state.selectedMonth;
      }
      this.setReportTitle(title);
    } else if (
      this.props.auth.role &&
      forReportRoles.country.includes(this.props.auth.role.role)
    ) {
      let title = "Placement Report - " + this.props.auth.role.role;
      if (this.props.basic.academic_calender) {
        title =
          title +
          " - " +
          this.props.basic.academic_calender.academic_year_name +
          " - Month of " +
          this.state.selectedMonth;
      }
      this.setReportTitle(title);
    }
    // UPDATE CONTENT =============================
    let arrayNew: reportToPrintListType[] = [];
    let n: number = 1;
    const allData = this.getUserList();

    if (allData) {
      for (const key in allData) {
        // if (Object.prototype.hasOwnProperty.call(allData, key)) {
        let dataArray: reportToPrintListType = {
          no: n,
          full_name: allData[key].full_name ? allData[key].full_name! : "",
          first_name: allData[key].first_name ? allData[key].first_name! : "",
          middle_name: allData[key].middle_name
            ? allData[key].middle_name!
            : "",
          last_name: allData[key].last_name ? allData[key].last_name! : "",
          nid: allData[key].nid ? allData[key].nid! : "",
          sex: allData[key].sex ? allData[key].sex! : "",
          qualification: allData[key].qualification
            ? allData[key].qualification!
            : "",
          position: allData[key].position ? allData[key].position! : "",
          school_name: allData[key].school
            ? FC_GetStringValue(allData[key].school!.school_name)
            : "",
          district_name: allData[key].location
            ? FC_GetStringValue(allData[key].location!.district_name)
            : "",
          role: allData[key].role ? allData[key].role! : "",
          phone_numbers: allData[key].phone_numbers
            ? allData[key].phone_numbers!
            : "",
          email: allData[key].email ? allData[key].email! : "",
          dob: FC_isDateValid(allData[key].dob) ? allData[key].dob! : "",
          marital_status: allData[key].marital_status
            ? allData[key].marital_status!
            : "",
          rssb_number: allData[key].rssb_number
            ? allData[key].rssb_number!
            : "",
          nationality:
            allData[key].nationality !== undefined &&
            allData[key].nationality !== null
              ? allData[key].nationality!
              : "",
          bank_name: allData[key].bank_name ? allData[key].bank_name! : "",
          bank_account: allData[key].bank_account
            ? allData[key].bank_account!
            : "",
          specialization: allData[key].specialization
            ? allData[key].specialization!
            : "",
          staff_category: allData[key].staff_category
            ? allData[key].staff_category!
            : "",
          staff_code: allData[key].staff_code ? allData[key].staff_code! : "",
          date_in_school: FC_isDateValid(allData[key].start_date_in_the_school)
            ? allData[key].start_date_in_the_school!
            : "",
          date_of_hire:
            allData[key].hired_date !== undefined
              ? FC_GetStringValue(allData[key].hired_date!)
              : "",
          status: allData[key].status ? allData[key].status! : "",
          school_code: allData[key].school
            ? FC_GetStringValue(allData[key].school!.school_code)
            : "",
          school_category: allData[key].school
            ? FC_GetStringValue(allData[key].school!.school_category)
            : "",
          school_status: allData[key].school
            ? FC_GetStringValue(allData[key].school!.school_status)
            : "",
          region_code: allData[key].school
            ? FC_GetStringValue(allData[key].school!.region_code)
            : "",
          village_name: allData[key].location
            ? FC_GetStringValue(allData[key].location!.village_name)
            : "",
          cell_name: allData[key].location
            ? FC_GetStringValue(allData[key].location!.cell_name)
            : "",
          cell_code: allData[key].location
            ? FC_GetStringValue(allData[key].location!.cell_code)
            : "",
          sector_name: allData[key].location
            ? FC_GetStringValue(allData[key].location!.sector_name)
            : "",
          sector_code: allData[key].location
            ? FC_GetStringValue(allData[key].location!.sector_code)
            : "",

          district_code: allData[key].location
            ? FC_GetStringValue(allData[key].location!.district_code)
            : "",
          province_name: allData[key].location
            ? FC_GetStringValue(allData[key].location!.province_name)
            : "",
          province_code: allData[key].location
            ? FC_GetStringValue(allData[key].location!.province_code)
            : "",
          subjects: FC_GetHierarchStrings(allData[key].teacher_hierarch)
            .courses,
          grades: FC_GetHierarchStrings(allData[key].teacher_hierarch).grades,
          periods: FC_GetHierarchStrings(allData[key].teacher_hierarch).periods,
        };
        for (const key in this.state.reportContent) {
          if (
            Object.prototype.hasOwnProperty.call(this.state.reportContent, key)
          ) {
            if (!this.state.reportContent[key].status) {
              delete dataArray[this.state.reportContent[key].key];
            }
          }
        }
        arrayNew.push(dataArray);
        n++;
        // }
      }
      this.setState({ reportToPrint: arrayNew });
      arrayNew = [];
      n = 1;
    }
  };

  resetReportContent = (currentState: boolean) => {
    let newArray: PlacementReportActiveContentInterface[] =
      this.state.reportContent;
    for (const key in newArray) {
      if (newArray[key].isChangeAllowed) newArray[key].status = !currentState;
    }

    this.setReportContent(newArray);
    this.setSelectAll(!currentState);
    this.updateDataToPrint();
  };

  updateReportContent = (key: PlacementReportActiveContentKeyInterface) => {
    let index: number = this.state.reportContent.findIndex(
      (item) => item.key === key
    );
    let newArray: PlacementReportActiveContentInterface[] =
      this.state.reportContent;
    newArray[index].status = !newArray[index].status;
    this.setReportContent(newArray);
    let isSelectAllNeeded = newArray.find((item) => item.status === false);
    if (isSelectAllNeeded) {
      this.setSelectAll(false);
    } else {
      this.setSelectAll(true);
    }

    // UPDATE APPLICATION LIST FOR UPDATE ==============================
    this.updateDataToPrint();
  };

  getUserList = (): TeacherData[] | null => {
    let result: TeacherData[] = [];

    if (this.state.reportAPIData === null) return null;
    const reportAPIData = this.state.reportAPIData.filter(
      (item) => (item.month = this.state.selectedMonth)
    );
    for (const key in reportAPIData) {
      const element: TeacherData[] = JSON.parse(reportAPIData[key].list);

      const updatedElement: TeacherData[] = this.changeSchoolData(element, {
        name: reportAPIData[key].school_name,
        code: reportAPIData[key].school_code,
      });

      result = [...result, ...updatedElement];
    }

    console.log("=================getUserList===================");
    console.log({ getUserList: result });
    console.log("=================getUserList===================");

    return result;
  };

  changeSchoolData = (
    thisData: TeacherData[],
    school: { code: string; name: string }
  ): TeacherData[] => {
    let newArray: TeacherData[] = [];
    for (const teacher of thisData) {
      let thisSchool = teacher.school;
      if (thisSchool) {
        thisSchool.school_code = school.code;
        thisSchool.school_name = school.name;
      }
      newArray.push({ ...teacher, school: thisSchool });
    }

    return newArray;
  };

  generatePages = (total_users: number): JSX.Element => {
    const totalPages = Math.ceil(total_users / this.state.tablePageLength);
    const startPage: number = 1;
    let _pages = Array.from(Array(totalPages + 1 - startPage).keys())
      .map((i) => startPage + i)
      .slice(0, 5);

    if (totalPages > 4 && this.state.currentPage > 2) {
      if (totalPages - this.state.currentPage > 1) {
        _pages = [
          this.state.currentPage - 2,
          this.state.currentPage - 1,
          this.state.currentPage,
          this.state.currentPage + 1,
          this.state.currentPage + 2,
        ];
      } else if (totalPages - this.state.currentPage === 1) {
        _pages = [
          this.state.currentPage - 3,
          this.state.currentPage - 2,
          this.state.currentPage - 1,
          this.state.currentPage,
          this.state.currentPage + 1,
        ];
      } else {
        _pages = [
          this.state.currentPage - 4,
          this.state.currentPage - 3,
          this.state.currentPage - 2,
          this.state.currentPage - 1,
          this.state.currentPage,
        ];
      }
    }

    let jumpToFirst: boolean = false,
      jumpToLast: boolean = false,
      moveBack: boolean = false,
      moveNext: boolean = false;

    if (totalPages > 1 && this.state.currentPage !== 1) moveBack = true;

    if (totalPages > 1 && this.state.currentPage !== totalPages)
      moveNext = true;

    if (totalPages > 3 && this.state.currentPage > 3) jumpToFirst = true;

    if (
      totalPages > 3 &&
      this.state.currentPage !== totalPages &&
      totalPages - this.state.currentPage > 2
    )
      jumpToLast = true;

    return (
      <div className="flex text-gray-700">
        {jumpToFirst && (
          <button
            className="h-12 w-12 ml-1 flex justify-center items-center rounded-full bg-gray-100 hover:bg-gray-200 cursor-pointer"
            onClick={() => this.setCurrentPage(1)}
          >
            <HiChevronDoubleLeft className="w-6 h-6" />
          </button>
        )}
        {moveBack && (
          <button
            className="h-12 w-12 ml-1 flex justify-center items-center rounded-full bg-gray-100 hover:bg-gray-200"
            onClick={() => this.setCurrentPage(this.state.currentPage - 1)}
          >
            <HiChevronLeft className="w-6 h-6" />
          </button>
        )}
        <div className="flex h-12 font-medium rounded-full mx-1 bg-gray-100">
          {_pages.map((item, index) => (
            <button
              className={`w-12 md:flex justify-center items-center gap-2 rounded-full ${
                this.state.currentPage === item
                  ? "bg-gray-600 hover:bg-gray-500 text-white cursor-auto"
                  : "hover:bg-gray-200"
              }`}
              key={Math.random()}
              disabled={this.state.currentPage === item}
              onClick={() => {
                this.setCurrentPage(item);
                // this.reloadUsers(item);
              }}
            >
              {item}
            </button>
          ))}
        </div>
        {moveNext && (
          <button
            className="h-12 w-12 mr-1 flex justify-center items-center rounded-full bg-gray-100 hover:bg-gray-200 cursor-pointer"
            onClick={() => {
              this.setCurrentPage(this.state.currentPage + 1);
              // this.reloadUsers(this.state.currentPage + 1);
            }}
          >
            <HiChevronRight className="w-6 h-6" />
          </button>
        )}
        {jumpToLast && (
          <button
            className="h-12 w-12 mr-1 flex justify-center items-center rounded-full bg-gray-100 hover:bg-gray-200 cursor-pointer"
            onClick={() => {
              this.setCurrentPage(totalPages);
              // this.reloadUsers(totalPages);
            }}
          >
            <HiChevronDoubleRight className="w-6 h-6" />
          </button>
        )}
      </div>
    );
  };

  render() {
    return (
      <>
        {/* REPORT HEADER WITH TITLE, SESSION, DONE DATE, DISTRICT ================ */}
        <DashboardContainer>
          <section className="flex flex-col sm:flex-row justify-between">
            <div className="px-3">
              <Heading size="lg">Monthly Teacher Placement Report</Heading>
              <p className="opacity-50">Placement report</p>
            </div>
            <div className="px-3 float-right">
              <div className="flex flex-row items-center justify-end gap-1 text-xl font-bold">
                <span className="text-gray-500">Period:</span>
                <p>
                  {this.props.basic.academic_calender
                    ? this.props.basic.academic_calender.academic_year_name
                    : ""}
                </p>
              </div>
              <div className="flex flex-row items-center justify-end gap-1 text-lg">
                <span className="text-gray-500">Done on</span>
                <p>{DATE(Date())}</p>
              </div>
            </div>
          </section>

          <section className="flex flex-col sm:flex-row justify-between mt-4">
            <div className="px-3">
              {this.props.auth.role &&
              forReportRoles.school.includes(this.props.auth.role.role) ? (
                <>
                  <Heading size="lg">
                    {this.props.auth.school
                      ? this.props.auth.school.school_name
                      : ""}
                  </Heading>
                  <p className="opacity-50">
                    {this.props.auth.district
                      ? this.props.auth.district.district_name + " district"
                      : ""}
                  </p>
                </>
              ) : this.props.auth.role &&
                forReportRoles.district.includes(this.props.auth.role.role) ? (
                <Heading size="lg">
                  {this.props.auth.district
                    ? this.props.auth.district.district_name + " district"
                    : ""}
                </Heading>
              ) : (
                this.props.auth.role &&
                forReportRoles.country.includes(this.props.auth.role.role) && (
                  <Heading size="lg">
                    {this.props.auth.role ? this.props.auth.role.role : ""}
                  </Heading>
                )
              )}
            </div>
            <div className="px-3 float-right">
              <div className="flex flex-row items-center justify-end font-bold">
                {/* <BiExport className="text-2xl text-gray-500" /> */}
                {/* <span className="text-gray-500">Download as</span> */}
                <span className="text-gray-500">Download</span>
                <button
                  className={`px-4 py-1 ml-1 rounded bg-white border hover:text-green-600 font-bold hover:bg-green-50 border-gray-200 ${
                    (this.state.loading ||
                      this.state.reportAPIData === null ||
                      this.state.reportAPIData.length === 0) &&
                    "cursor-not-allowed"
                  }`}
                  disabled={
                    this.state.loading ||
                    this.state.reportAPIData === null ||
                    this.state.reportAPIData.length === 0
                  }
                  onClick={() => {
                    this.updateDataToPrint();
                    this.setGenerateReport(true);
                  }}
                >
                  EXCEL
                </button>
                <button
                  className={`hidden px-4 py-1 ml-1 rounded bg-white border hover:text-red-600 font-bold hover:bg-red-50 border-gray-200 ${
                    this.state.downloading && "cursor-wait"
                  }`}
                  //   onClick={() => FC_GeneratePDF()}
                  //   disabled={loading || this.state.downloading}
                >
                  PDF
                </button>
              </div>
            </div>
          </section>
        </DashboardContainer>
        {/* END REPORT HEADER WITH TITLE, SESSION, DONE DATE, DISTRICT ================ */}

        {/* *********************************************************************** */}

        {/* FILTER BY ACADEMIC & MONTH ================ */}
        <DashboardContainer>
          <div className="border border-t mt-2 mb-1 flex flex-col md:flex-row items-center px-3 rounded gap-2">
            <div className="">
              <InputSelect
                data={
                  this.props.placement.academics
                    ? [
                        ...this.props.placement.academics.map((el) => ({
                          title: el.academic_year_name,
                          value: el.academic_year_id,
                        })),
                      ]
                    : []
                }
                error=""
                name="select_academic"
                onChange={(e) => {
                  this.setState({ selectedAcademic: e.target.value });
                  this.loadDataFormAPI(
                    this.state.selectedMonth,
                    e.target.value
                  );
                }}
                label="Select academic"
                placeholder="Select academic"
                value={this.state.selectedAcademic}
                size="sm"
                disabled={
                  this.state.loadingAcademics ||
                  this.state.loading ||
                  this.props.placement.academics === null
                }
              />
            </div>
            <div className="">
              <InputSelect
                data={[
                  ...MONTHS_LIST.map((el) => ({
                    title: el,
                    value: el,
                  })),
                ]}
                error=""
                name="select_month"
                onChange={(e) => {
                  this.setState({ selectedMonth: e.target.value });
                  this.loadDataFormAPI(
                    e.target.value,
                    this.state.selectedAcademic
                  );
                }}
                label="Select month"
                placeholder="Select month"
                value={this.state.selectedMonth}
                size="sm"
                disabled={
                  this.state.loadingAcademics ||
                  this.state.loading ||
                  this.props.placement.academics === null
                }
              />
            </div>
          </div>
        </DashboardContainer>
        {/* END FILTER BY ACADEMIC & MONTH ================ */}

        {/* *********************************************************************** */}

        {/* *********************************************************************** */}

        {/* REPORT SUMMARY ================ */}
        {/* <DashboardContainer>
          <ReportSummary />
        </DashboardContainer> */}
        {/* END REPORT SUMMARY ================ */}

        {/* *********************************************************************** */}

        {/* REPORT CONTENT ================ */}
        <DashboardContainer>
          <div className="border border-t mt-2 mb-1 flex flex-col md:flex-row items-center px-3 rounded">
            <div className="flex-1 pr-2">
              <TextInput
                label="Search"
                name="search"
                type="search"
                onChange={(e: any) => this.setSearchInput(e.target.value)}
                placeholder="Search by names, codes,..."
                value={this.state.searchInput}
                disabled={
                  !this.getUserList() ||
                  this.getUserList()!.slice(
                    this.state.currentPage - 1,
                    this.state.currentPage - 1 + this.state.tablePageLength
                  ).length === 0
                }
                size="sm"
                error=""
              />
            </div>
            <div className="">
              <InputSelect
                data={[
                  { title: tablePageLimit.toString(), value: tablePageLimit },
                  {
                    title: (tablePageLimit * 2).toString(),
                    value: tablePageLimit * 2,
                  },
                  {
                    title: (tablePageLimit * 3).toString(),
                    value: tablePageLimit * 3,
                  },
                  {
                    title: (tablePageLimit * 4).toString(),
                    value: tablePageLimit * 4,
                  },
                  {
                    title: (tablePageLimit * 5).toString(),
                    value: tablePageLimit * 5,
                  },
                ]}
                error=""
                name="page_limit"
                onChange={(e) =>
                  this.setState({ tablePageLength: Number(e.target.value) })
                }
                label="Show"
                placeholder="page limit"
                value={this.state.tablePageLength}
                size="sm"
              />
            </div>
          </div>
          <div className="w-full overflow-x-auto">
            <table className="w-full shadow" id="report_content">
              <thead className="bg-white border-b-2 border-gray-300 text-gray-600 font-bold text-sm">
                <tr>
                  <td className="px-2 py-2">#</td>
                  <td
                    className="px-2 py-2 cursor-pointer"
                    onClick={() => this.setSort("staff_code")}
                  >
                    <div className="flex items-center gap-1">
                      Staff Code <BiSort />
                    </div>
                  </td>
                  <td
                    className="px-2 py-2 cursor-pointer"
                    onClick={() => this.setSort("staff_nid")}
                  >
                    <div className="flex items-center gap-1">
                      Staff NID <BiSort />
                    </div>
                  </td>
                  <td
                    className="px-2 py-2 cursor-pointer"
                    onClick={() => this.setSort("full_name")}
                  >
                    <div className="flex items-center gap-1">
                      Names <BiSort />
                    </div>
                  </td>
                  <td
                    className="px-2 py-2 cursor-pointer"
                    onClick={() => this.setSort("dob")}
                  >
                    <div className="flex items-center gap-1">
                      DOB <BiSort />
                    </div>
                  </td>
                  <td
                    className="px-2 py-2 cursor-pointer"
                    onClick={() => this.setSort("sex")}
                  >
                    <div className="flex items-center gap-1">
                      Sex <BiSort />
                    </div>
                  </td>
                  <td
                    className="px-2 py-2 cursor-pointer"
                    onClick={() => this.setSort("qualification")}
                  >
                    <div className="flex items-center gap-1">
                      Qualification <BiSort />
                    </div>
                  </td>
                  <td className="px-2 py-2 cursor-pointer">School</td>
                  <td
                    className="px-2 py-2 cursor-pointer"
                    onClick={() => this.setSort("position")}
                  >
                    <div className="flex items-center gap-1">
                      Position <BiSort />
                    </div>
                  </td>
                </tr>
              </thead>
              <tbody>
                {this.state.loading ? (
                  <TableLoading cols={11} />
                ) : this.getUserList() === null ? (
                  <tr>
                    <td colSpan={11} className="px-2 py-2">
                      <div className="flex flex-col text-gray-400 items-center justify-center bg-gray-100 rounded py-10">
                        <ImFilesEmpty className="text-8xl text-gray-400 mb-2 animate__animated animate__shakeX" />
                        <span className="text-sm font-bold">
                          Something went wrong, try again!
                        </span>
                      </div>
                    </td>
                  </tr>
                ) : search(
                    this.getUserList()!.slice(
                      this.state.currentPage - 1,
                      this.state.currentPage - 1 + this.state.tablePageLength
                    ),
                    this.state.searchInput
                  ).length === 0 ? (
                  <tr>
                    <td colSpan={11} className="px-2 py-2">
                      <div className="flex flex-col text-gray-400 items-center justify-center bg-gray-100 rounded py-10">
                        <ImFilesEmpty className="text-8xl text-gray-400 mb-2 animate__animated animate__shakeX" />
                        <span className="text-sm font-bold">
                          No result found! Please try to search again
                        </span>
                      </div>
                    </td>
                  </tr>
                ) : (
                  sortThisArray(
                    !isEmptyOrSpaces(this.state.sortProperty),
                    search(
                      this.getUserList()!.slice(
                        this.state.currentPage - 1,
                        this.state.currentPage - 1 + this.state.tablePageLength
                      ),
                      this.state.searchInput
                    ),
                    this.state.sortProperty,
                    this.state.sortOrder
                  ).map((item: TeacherData, i) => (
                    <tr
                      key={i + 1}
                      className={`${
                        i % 2 !== 0 ? "bg-gray-100" : "bg-white"
                      } text-gray-600 hover:bg-accent-100 hover:text-accent-700 border-b border-gray-300 hover:border-accent-500 text-sm`}
                    >
                      <td className="px-2 py-2">
                        {(this.state.currentPage - 1) *
                          this.state.tablePageLength +
                          i +
                          1}
                      </td>
                      <td className="px-2 py-2">
                        {item.staff_code ? item.staff_code : ""}
                      </td>
                      <td className="px-2 py-2">{item.nid ? item.nid : ""}</td>
                      <td className="px-2 py-2">
                        {item.full_name ? item.full_name : ""}
                      </td>
                      <td className="px-2 py-2">{item.dob ? item.dob : ""}</td>
                      <td className="px-2 py-2">{item.sex ? item.sex : ""}</td>
                      <td className="px-2 py-2">
                        {item.qualification ? item.qualification : ""}
                      </td>
                      <td className="px-2 py-2">
                        {item.school ? item.school.school_name : ""}
                      </td>
                      <td className="px-2 py-2">
                        {item.position ? item.position : ""}
                      </td>
                    </tr>
                  ))
                )}
              </tbody>
            </table>
          </div>
          {/* pagination ========= */}
          {!this.state.loading && (
            <div className="flex flex-col items-center mt-3 mb-2 overflow-x-auto">
              {this.getUserList() &&
                this.generatePages(this.getUserList()!.length)}
            </div>
          )}
        </DashboardContainer>
        {/* END REPORT CONTENT ================ */}

        {/* ********************************************************************************** */}
        {/* GENERATE DYNAMIC REPORT CONTENT MODAL */}
        {this.state.generateReport && (
          <Modal
            title={
              <div className="flex flex-row gap-4 items-center">
                {/* <IoBriefcase className="text-5xl text-gray-500" /> */}
                <div className="flex flex-col">
                  <span className="font-bold text-2xl text-gray-500">
                    Generating Report contents
                  </span>
                  <span className="text-sm text-blue-600 font-normal">
                    Generating Excel Report
                  </span>
                </div>
              </div>
            }
            backDrop={true}
            theme={Themes.default}
            close={() => this.setGenerateReport(false)}
            backDropClose={false}
            widthSizeClass={ModalSize.medium}
            displayClose={true}
            padding={{ title: true }}
          >
            <div className="px-6">
              <div className="my-2 text-base text-gray-700 sm:text-lg">
                <TextInput
                  disabled={this.state.loading}
                  label="Report title"
                  name="report_title"
                  type="text"
                  value={this.state.reportTitle}
                  placeholder={"Report title"}
                  className="p-2"
                  onChange={(e) => {
                    this.setFormError(null);
                    this.setReportTitle(e.target.value);
                  }}
                  error={
                    this.state.formError &&
                    this.state.formError.element === "report_title"
                      ? this.state.formError.msg
                      : ""
                  }
                />
                <div className="flex flex-col mt-2">
                  <label>Report content</label>
                  <div className="grid grid-cols-2 md:grid-cols-3 items-center gap-2 mt-2">
                    {this.state.reportContent.map((item, index) => (
                      <div
                        key={index}
                        className={`flex items-center gap-1 ${
                          item.isChangeAllowed
                            ? "cursor-pointer"
                            : "cursor-not-allowed"
                        }`}
                        onClick={() =>
                          item.isChangeAllowed &&
                          this.updateReportContent(item.key)
                        }
                        title={
                          !item.isChangeAllowed
                            ? "Required for every report!"
                            : ""
                        }
                      >
                        {item.status ? (
                          <MdCheckBox className="w-6 h-6 text-blue-700" />
                        ) : (
                          <MdCheckBoxOutlineBlank className="w-6 h-6" />
                        )}
                        <span>{item.title}</span>
                      </div>
                    ))}
                  </div>
                  {this.state.formError &&
                    this.state.formError.element === "report_content" && (
                      <span className="text-red-500">
                        {this.state.formError.msg}
                      </span>
                    )}
                </div>
              </div>
              <div className="flex justify-between mb-2 mt-4">
                <div
                  className={`flex items-center gap-1 cursor-pointer`}
                  onClick={() => this.resetReportContent(this.state.selectAll)}
                >
                  {this.state.selectAll ? (
                    <MdCheckBox className="w-6 h-6 text-blue-700" />
                  ) : (
                    <MdCheckBoxOutlineBlank className="w-6 h-6" />
                  )}
                  <span>Select all</span>
                </div>

                {!this.state.loading && this.state.reportToPrint && (
                  <ExportToExcel
                    fileData={this.state.reportToPrint}
                    fileName={this.state.reportTitle}
                    btnName="Download"
                  />
                )}
              </div>
            </div>
          </Modal>
        )}
        {/* END GENERATE DYNAMIC REPORT CONTENT MODAL */}
      </>
    );
  }
}

const mapStateToProps = ({
  auth,
  basic,
  placement,
}: StoreState): { auth: Auth; basic: Basic; placement: Placement } => {
  return { auth: auth, basic: basic, placement: placement };
};

export const PlacementReport = connect(mapStateToProps, {
  FC_GetPlacementByDistrict,
  FC_GetAcademics,
})(_PlacementReport);
